.contacts_list {
    padding: 0;
    margin-left: 10px;
}

.contact_item{
    display: flex;
    align-items: center;
        font-size: 30px;
        padding: 5px 0;

}

.btn {
    cursor: pointer;
    border-radius: 5px;
    font-size: 20px;
    height: 30px;
    margin-left: 30px;
    border: none;
    background-color: aquamarine;
}

.btn:hover {
    background-color: aqua;
}

.status{
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: #000;
    margin-right: 15px;
}